<template>
    <div class="container back-two" v-if="gameList">

        <div class="trans-form-page">

                <div class="trans-form-card">
                    <div class="wrap" style="height: 2.5rem;">
                        <div style="width: 48%;float: left;text-align: center;border-right: #CEC8D8 1px solid;padding: 0.1rem 0;margin: 0.1rem 0;">
                            <p style="font-size: 0.3rem;color: #F5EFEF;">账户余额</p>
                            <div class="balanceBox" style="color: #fff;padding: 0.15rem 0;">
                                <b v-if="UserBaseInfo.balance" style="font-size: 0.4rem;">{{UserBaseInfo.balance | fixed(3)}}</b>
                                <b v-else style="font-size: 0.4rem;">0.000</b>
                                <span @click="resref('111', 5)" :class="['icon-refresh-gray', {'acti': ref===5}]" style="position: absolute;top: 1rem;"></span>
                            </div>
                        </div>
                        <div style="width: 48%;float: right;text-align: center;padding: 0.1rem 0;margin: 0.1rem 0;">
                            <p style="font-size: 0.3rem;color: #F5EFEF;">提款打码量</p>
                            <div class="balanceBox" style="color: #fff;padding: 0.15rem 0;">
                                <b v-if="dml !=''" style="font-size: 0.4rem;">{{dml | fixed(3)}}</b>
                                <b v-else style="font-size: 0.4rem;">0.000</b>
                                <img @click="whatIsThis" src="../../static/img/edbg/dama.png" style="position: absolute;top: 0.5rem;height: 0.3rem;right: 0.9rem;">
                                <span @click="resref('222', 6)" :class="['icon-refresh-gray', {'acti': ref===6}]" style="position: absolute;top: 1rem;"></span>
                            </div>
                        </div>
                        <div class="btnBox" style="color: #F5F4F4;">
                            <button @click="tiaozhuan" style="width: 1.5rem;height:0.6rem;margin:0 0.1rem;border: 1px solid #F5F4F4; border-radius: 0.3rem;">转换记录</button>
                            <button @click="tiaozhuan1" style="width: 1.5rem;height:0.6rem;margin:0 0.1rem;border: 1px solid #F5F4F4; border-radius: 0.3rem;">资金明细</button>
                            <button @click="tiaozhuan2" style="width: 1.5rem;height:0.6rem;margin:0 0.1rem;border: 1px solid #F5F4F4; border-radius: 0.3rem;">立即充值</button>
                            <button @click="tiaozhuan3" style="width: 1.5rem;height:0.6rem;margin:0 0.1rem;border: 1px solid #F5F4F4; border-radius: 0.3rem;">现金提取</button>
                        </div>
                    </div>
                </div>

                <div class="trans-form-page-list">
                    <ul>
                        <template  v-for="(item, index) in gameList">
                            <li :class="'li' + index" :key="index" v-if="item.show">
                            <div class="img-wrap">
                                <img :src="importImg(item.gameCode)" alt="">
                                <h2>{{item.gameName}}</h2>
                            </div>
                            <div style="height: 2rem;padding: 0 0.2rem;" v-if="!item.maintaining">
                                <div class="money" style="width: 60%;float: left;margin-top: 0.2rem;height: 0.5rem;line-height: 0.5rem;">
                                    <h3 style="float:left;margin-right: 0.1rem;">{{item.money | fixed(3)}}</h3>
                                    <span @click="resref(item.gameCode, index)" :class="['icon-refresh-gray', {'acti': ref===index}]" style="float: left;"></span>
                                </div>
                                <div class="change-" style="width: 40%;float: right;margin-top: 0.2rem;">
                                    <div>
                                        <button style="background: -webkit-linear-gradient(left,#4a93ff,#30bfff);width:1.1rem;height:.5rem;border-radius:.5rem;color:#fff;" @click="to_change(item.gameCode, true, true)">转入</button>
                                        <button style="background: -webkit-linear-gradient(left,#4a93ff,#30bfff);width:1.1rem;height:.5rem;border-radius:.5rem;color:#fff;margin-left:.2rem;" @click="to_change(item.gameCode, false, true, item.money)">转出</button>
                                    </div>
                                </div>
                            </div>
                            <div style="height: 2rem;padding: 0 0.2rem;" v-if="item.maintaining">
                                <div class="maintaining">维护中</div>
                            </div>

                        </li>
                        </template>
                    </ul>
                </div>

        </div>
    </div>
    <loadding v-else></loadding>
</template>

<script>

    import { mapState } from "vuex";
    import $ from "jquery";

    import {
        GAME_GET_USER_BASE_INFO
    } from "@/tool/common/constants.js";

    import { getToday, lastDay, thisMonth, getTomorrow } from "@/tool/common/time.js";
    import { companyShortName } from "@/tool/common/config.js";

    export default {
        data(){
            return {
                gameList: false,
                config: {},
                UserSession: {},
                dml:"",
                ref: -1,
                money: '',
            }
        },
        watch: {

        },
        computed:{
            ...mapState('Game', {
                UserBaseInfo: state => state[GAME_GET_USER_BASE_INFO],
            }),
        },
        created(){
            this.config = this.$route.query

            this.getUserBaseInfo();
            this.getGameList();
            const {uid, token} = this.$route.query;
            this.config.uid = uid;
            this.config.token = token;
            var user = this.$user()
            this.$store.commit('User/SET_USER', user);
            if(this.env.app){
                var json = '{"title":"额度转换","showNai":"y","back":"app"}'
                this.callHandle(json)
            }
        },
        mounted(){
            this.getUserSession();
        },
        methods: {
            getUserSession(){
                const _this = this
                this.$api.user.getUserSession(this.baseUser)
                    .then(res=>{
                        _this.dml = res.drawingMoney
                        _this.UserSession = res
                    })
            },
            getUserSession1(){
                const _this = this
                this.$api.user.getUserSession(this.baseUser)
                    .then(res=>{
                        _this.dml = res.drawingMoney
                        _this.ref = -1
                        this.$toast('刷新完成');
                    })
            },
            whatIsThis(){
                this.mui.confirm(
                    "&nbsp;&nbsp;&nbsp;提款打码量，是指可以结算的提款数量，每笔投注都会计算打码量。\n" +
                    "&nbsp;&nbsp;&nbsp;例：投注100元，即产生100元提款打码量。",
                    "温馨提示",
                    ["了解"]
                );
            },
            tiaozhuan(){
                this.$router.push('/capitalRecord1?uid='+ this.config.uid +'&token=' + this.config.token);
            },
            tiaozhuan1(){
                this.$router.push('/capitalRecord?uid='+ this.config.uid +'&token=' + this.config.token);
            },
            tiaozhuan2(){
                this.$router.push('/recharge?uid='+ this.config.uid +'&token=' + this.config.token);
            },
            tiaozhuan3(){
                this.$router.push('/withdrawal?uid='+ this.config.uid +'&token=' + this.config.token);
            },
            to_change(code, is, all=false, money=0){
                if(money <= 1 && !is){
                    this.$toast('游戏余额不足');
                    return false;
                }
                this.money = parseInt(money);
                switch (code) {
                    case '1000001359742941':
                        return this.kaiyuan(is, all)
                    case '1000000470622605':
                        return this.vg(is, all)
                    case '1000001350416944':
                        return this.fg(is, all)
                    case '1000001350416946':
                        return this.junbo(is, all)
                    default:
                        return 0
                }
            },

            // 引入图片
            importImg(e){
                switch (e) {
                    case '1000001359742941':
                        return require('../../static/img/b8d3db_60x47.png')
                    case '1000000470622605':
                        return require('../../static/img/b58b9c_200x200.png') // vg
                    case '1000001350416944':
                        return require('../../static/img/d121c6_340x340.png')
                    case '1000001350416946':
                        return require('../../static/img/060748_330x283.png')
                    default:
                        return require('../../static/img/6ffe86_187x74.png')
                }
            },

            // 刷新金额
            resref(e, index){

                switch (e) {
                    case '1000001359742941': // 开元棋牌
                        this.getAllAmount(true);
                        this.ref = index;
                        break;
                    case '1000000470622605': // VG棋牌
                        this.getVgAmount(true);
                        this.ref = index;
                        break;
                    case '1000001350416944': // FG棋牌
                        this.getLyAmount(true);
                        this.ref = index;
                        break;
                    case '1000001350416946': // 君博彩票
                        this.getMyBalance(true);
                        this.ref = index;
                        break;
                    case '111': // 开元棋牌
                        this.getUserBaseInfo(true);
                        this.ref = index;
                        break;
                    case '222': // VG棋牌
                        this.getUserSession1();
                        this.ref = index;
                        break;
                    default:
                        break;
                }

            },

            toggleTabs(tabs){
                this.tabs = tabs
                this.getNetData()
            },

            getNetData(){
                this.list = false
                var data = {
                    ...this.config,
                    pageIndex:1,
                    pageSize:20,
                    type:'',
                }
                switch (this.tabs) {
                    case 1:
                        data.startTime = getToday()
                        data.endTime = getTomorrow()
                        break;
                    case 2:
                        data.startTime = lastDay()
                        data.endTime = getToday()
                        break;
                    case 3:
                        var date = thisMonth()
                        data.startTime = date.thisMonthStart
                        data.endTime = date.thisMonthEnd
                        break;
                    default:
                        break;
                }
                this.$api.transformation.getLogUserCoinListBySC(data)
                .then(res=>{
                    console.log(res);
                    setTimeout(() => {
                       this.list = res.logUserCoinList
                    }, 1200);
                })
            },

            // 金额增加
            money_addend(num){
                this.money_tabs = num
                if(num){
                    this.money =  this.money *1 +  this.add_list[num]
                } else {
                    if(this.is){
                        this.money = parseInt(this.UserBaseInfo.balance)
                    }else{
                        var price = parseInt(this.actions[this.select].price)
                        if ( price >= 0){
                            this.money = price
                        }else{
                            this.money = 0
                        }
                    }
                }
            },

            // 选项选择
            onSelect(item) {
            // 可以通过 close-on-click-action 属性开启自动收起
                this.show = false;
                this.select = item.index
                this.$toast(`切换到 => ${item.name}`);
            },

            // 取消
            onCancel() {
                this.$toast('取消');
            },

            // 个人信息
            getUserBaseInfo(isref){
                if(isref){
                    this.ref = 4;
                }
                const {uid, token} = this.config
                var data = {
                    uid,
                    token,
                }
                this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, data)
                .then(()=>{
                    if(isref){
                        this.$toast('刷新完成')
                        this.ref = -1
                    }
                })
            },

            // 君博余额
            getMyBalance(is){
                this.$api.transformation.getMyBalance(this.config)
                .then(res=>{
                    var dex = this.getIndex('1000001350416946')
                    if(res.result == 1 ){
                        this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                        if(is){
                            this.ref = -1
                            this.$toast('刷新完成')
                        }
                    }
                })
            },

            // 开元余额
            getAllAmount(is){
                this.$api.transformation.getAllAmount(this.config)
                .then(res=>{
                    var dex = this.getIndex('1000001359742941')
                    this.gameList[dex].money = res.chessAmount >= 0 ? res.chessAmount : '获取失败'
                    if(is){
                        this.ref = -1
                        this.$toast('刷新完成')
                    }
                })
            },

            // VG余额
            getVgAmount(is){
                this.$api.transformation.getVgAmount(this.config)
                .then(res=>{
                    console.log(res);
                    var dex = this.getIndex('1000000470622605')
                    this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                    if(is){
                        this.ref = -1
                        this.$toast('刷新完成')
                    }
                })
            },

            // FG余额
            getLyAmount(){
                this.$api.transformation.getLyAmount(this.config)
                .then(res=>{
                    console.log(res);
                    var dex = this.getIndex('1000001350416944')
                    this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                    this.ref = -1
                })
            },

            // 游戏列表
            getGameList(){
                this.$api.transformation.getGameList()
                .then(res=>{
                    console.log(res);
                    let modify = res.data
                    modify.forEach(item=>{
                        item.money = 0;
                        item.show = true;
                        switch (item.gameCode){
                            case '1000001359742941':
                                item.gameName = '开元红包捕鱼'
                                break;
                            case '1000001350416944':
                                item.gameName = 'FG捕鱼'
                                break;
                            case '1000000470622605':
                                item.gameName = '财神捕鱼'
                                break;
                            case '1000001350416946':
                                item.show = false;
                                break;
                            default:
                                break;
                        }
                    })
                    this.gameList = modify;
                    this.getMyBalance();
                    this.getAllAmount();
                    this.getVgAmount();
                    this.getLyAmount();

                })
            },

            getIndex(code){
                var dex = -1;
                this.gameList.forEach((item, index)=>{
                    if(item.gameCode == code){
                        dex = index
                    }
                })
                return dex
            },

            // 确实转换
            to_ky(){
                var current = this.actions[this.select]
                console.log(current);
                console.log(this.is);
                if(this.money==''){
                    this.$toast('请输入金额');
                    return false;
                }else{
                    if(this.is){
                        if(this.UserBaseInfo.balance >= this.money){
                            this.to_change(current.code, true, false);
                        }else{
                            this.$toast('转入的金额不能大于自己的余额');
                        }

                    }else{
                        if(current.price >= this.money){
                            this.to_change(current.code, false, false);
                        }else{
                            this.$toast('转入的金额不能大于游戏的余额');
                        }
                    }
                }

            },

            // 开元棋牌
            kaiyuan(up, all){
                let data = {
                    ...this.config
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.userBalance = this.UserBaseInfo.balance
                        }else{
                            data.userBalance = this.money
                        }
                        // 上分
                        this.$api.transformation.changeAmount(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getAllAmount();
                            this.getUserBaseInfo();
                        })
                    }else{
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.chessAmount = this.money
                    // 下分
                    this.$api.transformation.unChangeAmount(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getAllAmount();
                        this.getUserBaseInfo();
                    })
                }
            },

            // 君博
            junbo(up, all){
                let data = {
                    ...this.config,
                    merchantName: companyShortName
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.money = this.UserBaseInfo.balance;
                        }else{
                            data.money = this.money;
                        }
                        // 上分到CP
                        this.$api.transformation.upToJb(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getUserBaseInfo();
                            this.getMyBalance();
                        })
                    }else {
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    // 从CP下分
                    this.$api.transformation.downFromJb(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getMyBalance();
                        this.getUserBaseInfo();
                    })
                }
            },

            // VG转换
            vg(up, all){
                console.log(up, all);
                let data = {
                    ...this.config
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.money = parseInt(this.UserBaseInfo.balance);
                        }else{
                            data.money = this.money;
                        }
                        this.$api.transformation.upToVg(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getVgAmount();
                            this.getUserBaseInfo();
                        })
                    } else {
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    this.$api.transformation.downVg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getVgAmount();
                        this.getUserBaseInfo();
                    })
                }
            },

            // FG转换
            fg(up, all){
                console.log(up);
                console.log(up, all);
                let data = {
                    ...this.config
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.money = parseInt(this.UserBaseInfo.balance);
                        }else{
                            data.money = this.money;
                        }
                        this.$api.transformation.upToFg(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getLyAmount();
                            this.getUserBaseInfo();
                        })
                    } else {
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    this.$api.transformation.downFg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getLyAmount();
                        this.getUserBaseInfo();
                    })
                }
            }
        },
        filters: {
            operType(val){
                var str =''
                switch (val) {
                    case 26:
                        str = '开元转余额'
                        break;
                    case 27:
                        str = '余额转开元'
                        break;
                    case 38:
                        str = '余额转乐游'
                        break;
                    case 42:
                        str = '余额转VG'
                        break;
                    case 43:
                        str = 'VG转余额'
                        break;
                    case 56:
                        str = '彩票转余额'
                        break;
                    case 57:
                        str = '余额转彩票'
                        break;
                    default:
                        break;
                }
                return str
            }
        }
    }
</script>

<style>
    .mui-popup-text{
        line-height: 1.3em;
        letter-spacing: 0.1em;
        text-align: left;
    }
</style>
